.mobile_item_subtitle {
	font-weight: 600;
	font-size: 21px;
	line-height: 31px;
	color: #FFFFFF;
	text-align: center; }

.mobile_item_plus {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	width: 26px;
	height: 26px;
	background-color: #000000;
	margin: 0 auto;
	border-radius: 100%;
	bottom: 35px;
	left: 50%;
	transform: translateX(-50%);
	cursor: pointer;
	&::before {
		content: '';
		position: absolute;
		display: block;
		width: 12px;
		height: 2PX;
		background-color: $main-color;
		transition: 0.3s all; }
	&:after {
		content: '';
		position: absolute;
		display: block;
		height: 12px;
		width: 2PX;
		background-color: $main-color;
		transition: 0.3s all; }
	&:hover {
		&::before, &:after {
			background-color: $second-color; } } }




